	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"As The World Churns","date":"14 Jul 2004","slug":"/2004-07-14-as-the-world-churns/","tags":"Y2004"},"html":"<p>We have new developments in the soap opera that is my workplace. Management finally presented a concrete proposal for salary reductions (retroactive to July 1). The reductions are to be progressive (rather than a flat 15%)  and require 75% acceptance to take effect. However, the reductions are still permanent and the employees still receive nothing in return... no layoff protection, no plan for future repayment, and no business plan for the future. So I'll vote to reject the plan. If that means layoffs, even for me, then so be it.</p>\n<p>At the same time, I've decided to stay with the company (rather than work elsewhere or go freelance) at least until the end of the year. The exception would be if a <a href=\"http://www.sixapart.com/\">blogging</a> <a href=\"http://www.blogg.de\">company</a> were interested in me, but otherwise I'll wait and see.</p>"},"next":{"fields":{"title":"Why do you work at a job you hate?","date":"13 Jul 2004","slug":"/2004-07-13-why-do-you-work/"}},"previous":{"fields":{"title":"For Sale: One Euro 2004 Ball","date":"14 Jul 2004","slug":"/2004-07-14-for-sale-one-euro-2004-ball/"}}},{"node":{"fields":{"title":"Why do you work at a job you hate?","date":"13 Jul 2004","slug":"/2004-07-13-why-do-you-work/","tags":"Y2004"},"html":"<p>In Germany, it's to keep your unemployment benefits (they are reduced if you quit as opposed to being fired).</p>\n<p>In the US, it's to keep your health insurance.</p>\n<p>Neither reason is the basis of rational planning.</p>"},"next":{"fields":{"title":"Frauenparkplatz","date":"13 Jul 2004","slug":"/2004-07-13-frauenparkplatz/"}},"previous":{"fields":{"title":"As The World Churns","date":"14 Jul 2004","slug":"/2004-07-14-as-the-world-churns/"}}},{"node":{"fields":{"title":"Frauenparkplatz","date":"13 Jul 2004","slug":"/2004-07-13-frauenparkplatz/","tags":"Y2004"},"html":"<p><a title=\"Two for the price of one...\" href=\"http://revirement.de/weblog/index.php?p=1499&c=1\">vasili: Frauenparkplatz</a></p>"},"next":{"fields":{"title":"The day disco died","date":"12 Jul 2004","slug":"/2004-07-12-the-day-disco-died/"}},"previous":{"fields":{"title":"Why do you work at a job you hate?","date":"13 Jul 2004","slug":"/2004-07-13-why-do-you-work/"}}}],"pathPrefix":"","first":false,"last":false,"index":454,"pageCount":1101}}

